/*==================================================
  Template Name: Friend Finder
  Description: A Social Network Template
  Author: thunder-team
  Version: 1.1
==================================================*/

/*==================================================
  [Table of Contents]
  --------------------------------------------------
  - body
    - Typography
    - Common classes
    - Header
    - Homepage
      - Top Banner
      - Signup Form
      - Features
      - App Download
      - Image Divider
      - Site Facts
      - Live Feed
    - Newsfeed 
      - Left Sidebar
      - Right Sidebar
      - Create Post Box
      - Post Contents
      - Nearby People
      - Friend List
      - Chat Room
      - Media: image and video 
    - Timeline
      - Timeline Sidebar
      - Post Date
      - About Profile
      - Profile Album
    - Contact Us
    - Footer
    - Media Queries
    - Slide Animations
    - Scroll Down Arrow
    - Preloader
==================================================*/

html, body {
  height: 100%;
  width: 100%;
}

body{
	font-family: 'Raleway', sans-serif;
	color: #6d6e71;
	font-size: 12px;
}


/*==================================================
  Typography
  ==================================================*/

@font-face {
    font-family:"Agency FB";
    src: url("./../fonts/agency-fb.eot") /* EOT file for IE */
}
@font-face {
    font-family:"Agency FB";
    src: url("./../fonts/agency-fb.ttf") /* TTF file for CSS3 browsers */
}

h1, h2, h3, h4, h5, h6{color: #27aae1;}

h1.grey, h2.grey, h3.grey, h4.grey, h5.grey, h6.grey{
  color: #6d6e71;
  margin: 0 auto 20px;
}

.text-white{color: #fff;}

.text-white:hover{color: #fff;}

.text-green{color: #8dc63f;}

.text-green:hover{color: #39b54a;}

.text-red{color: #ef4136;}

.text-red:hover{color: #d6100b;}

.profile-link{font-weight: 600;}

.text-muted{color: #939598;}

a{color: #27aae1;}

a:hover{color: #149AC9;}

a, a:active, a:focus {outline: none;}

section h1.section-title{
	font-family: 'Agency FB', sans-serif;
	font-size: 215px;
	color: #E6E7E8;
	margin-bottom: -55px;
}

section h2.sub-title{
	font-family: 'Agency FB', sans-serif;
	color: #E6E7E8;
	font-size: 50px;
	margin: 40px 0;
}


.following{
  color: #8dc63f;
  font-size: 12px;
  margin-left: 20px;
}


/*==================================================
  Common
  ==================================================*/

.container{max-width: 1050px;}

section .wrapper{
	padding-top: 100px;
	padding-bottom: 100px;
}

#page-contents{
  padding: 30px 0 50px;
}

.btn-primary{
	background: #27aae1;
	padding: 5px 20px;
	border: none;
	font-size: 14px;
	border-radius: 4px;
	color: #fff;
	position: relative;
  font-weight: 600;
  outline: none;
}

.btn-primary::before{
  content: "";
  background: rgba(255,255,255, 0.2);
  position: absolute;
  width: 95%;
  height: 1px;
  top: 1px;
  left: 0;
  right: 0;
  margin: auto;
}

.btn-primary:hover{
  background: #149AC9;
  transition: all 1s;
}

.btn-secondary{
	background: linear-gradient(to bottom, rgba(109,110,113,1), rgba(0,0,0,1));
	padding: 0 40px;
	border: none;
	min-height: 36px;
	font-size: 18px;
	border-radius: 6px;
	color: #fff;
	position: relative;
  outline: none;
}

.btn-secondary:hover{
	background: #000;
  transition: all 1s;
}
.btn-more {
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-top: 30px;
  padding: 10px 25px;
  font-size: 18px;
}
section img{
	margin: auto;
}

a.profile-photo{
  height: 58px;
  width: 58px;
  border-radius: 50%;
  overflow: hidden;
  display: block;
}

a.profile-photo-lg{
  height: 80px;
  width: 80px;
  border-radius: 50%;
  overflow: hidden;
  display: block;
}

a.profile-photo-md{
	height: 50px;
	width: 50px;
  border-radius: 50%;
  overflow: hidden;
  display: block;
}

a.profile-photo-sm{
	height: 40px;
	width: 40px;
  border-radius: 50%;
  overflow: hidden;
  display: block;
}

a.profile-photo-xs{
  height: 30px;
  width: 30px;
  border-radius: 50%;
  overflow: hidden;
  display: block;
}
.profile-photo img{
  width: 100%;
  height: auto;
}

.img-wrapper{
  position: relative;
}

.img-wrapper::after{
  content: "";
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  transition: all 1s ease 0s;
}

.img-wrapper:hover:after{
  background-color: rgba(0,0,0, 0.4);
}

ul{
  padding-left: 15px;
}

ul li{
  list-style-image: url("./../images/bullet.png");
}

.line-divider{
  background: none;
  height: 1px;
  border-top: 1px solid #e6e6e6;
  width: 100%;
  margin: auto;
  margin-bottom: 10px;
}

.map{
  width: 100%;
  height: auto;
  border: none;
}

.static{
  position: static;
}

textarea{
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;

	width: 100%;
}


/*==================================================
  Header
  ==================================================*/

#header{
  min-height: 50px;
}

.menu{
  background: #231F20;
  border: none;
  border-radius: 0;
  margin-bottom: 0;
}

.menu form{
  margin: 12px 0;
}

.menu form .form-group{
  position:relative;
}

.menu form .form-group i.icon{
  position: absolute;
  left: 15px;
  top: 5px;
  color: #fff;
}

.menu form .form-group input.form-control{
  background: rgba(255,255,255, 0.2);
  border: 0;
  box-shadow: none;
  min-width: 235px;
  color: #fff;
  height: 26px;
  border-radius: 13px;
  font-size: 12px;
  padding-left: 30px;
}

.menu ul.main-menu li.dropdown a{
  color: #fff;
  font-size: 12px;
  font-weight: 600;
}

.menu ul.main-menu li.dropdown a:hover, .menu ul.main-menu li.dropdown a:active, .menu ul.main-menu li.dropdown a:focus{
  color: #27aae1;
  background: none;
}

.menu ul.main-menu li.open a, .menu ul.main-menu li.open a:active, .menu ul.main-menu li.open a:focus{
  background: transparent;
  color: #27aae1;
}

.menu ul.main-menu li ul.dropdown-menu{
  background: #231F20;
  box-shadow: none;
  border: 0;
  padding: 0;
}

.menu ul.main-menu li ul.dropdown-menu li{
  list-style: none;
  padding: 5px 0;
  border-bottom: 1px solid rgba(255,255,255, 0.1)
}

.menu ul.main-menu li ul.dropdown-menu li a, .menu ul.main-menu li.open ul.dropdown-menu li a{
  color: #fff;
  font-size: 12px;
  line-height: 16px;
}

.menu ul.main-menu li ul.dropdown-menu li a:hover, .menu ul.main-menu li.open ul.dropdown-menu li a:hover{
  color: #e6e6e6;
}

.navbar-default .navbar-collapse, .navbar-default .navbar-form{
  border: 0;
}

/*For Homepage Header*/

#header.lazy-load{
  visibility: hidden;
  position: fixed;
  left: 0;
  right: 0;
  transition: .5s ease-in-out;
  transform: translateY(-50px);
  z-index: 99999;
}
#header.lazy-load.visible{
  visibility: visible;
  transform: translateY(0);
}


/*==================================================
  Homepage Top Banner
  ==================================================*/

#banner{
	background: url(./../images/bg/bg-1.jpg) fixed no-repeat;
  background-position: center;
	background-size: cover;
	width: 100%;
	min-height: 550px;
	position: relative;
	top: 0;
}

/*==================================================
  Homepage Signup Form
  ==================================================*/

.sign-up-form{
  background: linear-gradient(to bottom, rgba(43,57,144,.8), rgba(39,170,225,1) 65%);
  position: absolute;
  width: 340px;
  min-height: 600px;
  top: 0;
  padding: 40px 30px;
  text-align: center;
  color: #fff;
  z-index: 1000;
}

.sign-up-form:after{
  content: "";
  border-color: #27aae1 transparent transparent;
  border-style: solid;
  border-width: 40px 170px 0;
  bottom: -40px;
  height: 0;
  left: 0;
  position: absolute;
  width: 0;
}

.sign-up-form h2{
  font-family:'Agency FB', sans-serif;
  margin: 100px 0 20px;
}

.sign-up-form .line-divider{
  background: none;
  height: 1px;
  border-top: 1px solid rgba(255, 255, 255, .20);
  width: 180px;
  margin: auto;
  margin-bottom: 10px;
}

.sign-up-form .form-wrapper{
  padding: 20px 20px 0;
  border: 1px solid rgba(255, 255, 255, .2);
  border-top: none;
  margin-bottom: 30px;
}

.sign-up-form .form-wrapper .signup-text{
  font-size: 15px;
}

.sign-up-form .form-wrapper .form-group{
  margin-bottom: 10px;
  width: 100%;
}

.sign-up-form .form-wrapper .form-group .form-control{
  border: none;
  box-shadow: none;
  border-radius: 17px;
  padding-left: 20px;
}

.sign-up-form .form-wrapper button{
  position: relative;
  top: 20px;
  border-radius: 17px;
}

.sign-up-form a{
  color: #fff;
}

.sign-up-form img.form-shadow{
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  bottom: -80px;
}


/*==================================================
  Features Section
  ==================================================*/

#features{
	background: #EFEFEF;
	background: radial-gradient(#fff 50%, #EFEFEF 90%);
	background: -moz-radial-gradient(#fff 50%, #EFEFEF 90%);
	background: -webkit-radial-gradient(#fff 50%, #EFEFEF 90%);
	background: -o-radial-gradient(#fff 50%, #EFEFEF 90%);
	text-align: center;
}

#features .feature-item .feature-icon{
  background: linear-gradient(to bottom, rgba(43,57,144,.8), rgba(39,170,225,.8) 65%), url("./../images/blur.png") no-repeat;
  background-size: cover;
  background-position: center;
  width: 110px;
  height: 110px;
  border-radius: 55px;
  margin: auto;
  border: 6px solid #fff;
  box-shadow: 0 12px 12px rgba(39, 170, 225, 0.3);
  cursor: pointer;
  font-size: 35px;
  color: #fff;
  line-height: 100px;
}

#features .feature-item .feature-icon:hover{
  background: #27aae1;
  transition: all 1s;
}

#features .feature-item h3{
  font-family: 'Agency FB', sans-serif;
  color: #939598;
}

#incremental-counter .num {
  background: #f8f8f8 none repeat scroll 0 0;
  border: 1px solid #fff;
  border-radius: 4px;
  color: #27aae1;
  display: inline-block;
  height: 64px;
  line-height: 62px;
  margin: 0 4px 20px;
  position: relative;
  left: -1px;
  text-align: center;
  width: 50px;
  font-size: 45px;
  font-size: 3.72625em;
  font-weight: normal;
  font-family: 'Agency FB', sans-serif;
}
#incremental-counter .num::before {
  background: #00aae6;
  content: "";
  display: block;
  height: 1px;
  left: -1px;
  margin: -0.5px 0 0;
  position: absolute;
  right: -1px;
  top: 50%;
  width: auto;
}

#features img.face-map{
  margin: 0 auto -250px;
}


/*==================================================
  App Download Section
  ==================================================*/

#app-download{
  background: #fff;
  text-align: center;
}

#app-download ul.app-btn{
  padding: 0;
  margin: 0;
}

#app-download ul.app-btn li{
  list-style: none;
  padding-left: 10px;
  padding-right: 10px;
}

#app-download ul.app-btn li button{
  padding: 8px 15px;
}

#app-download ul.app-btn li button::after{
  content: "";
  background: linear-gradient(to bottom, rgba(0,0,0,.1), transparent 70%);
  height: 36px;
  width: auto;
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  border-radius: 6px;
}


/*==================================================
  Homepage Image Divider
  ==================================================*/

.img-divider{
  background: url("./../images/bg/bg-2.jpg") no-repeat;
  background-size: cover;
  text-align: center;
  min-height: 460px;
  background-position: center;
}


/*==================================================
  Site Facts Section
  ==================================================*/

#site-facts{
	background: radial-gradient(rgba(0, 0, 0, 0.7) 40%, rgba(0, 0, 0, 1) 100%), url("./../images/bg/bg-4.jpg") fixed no-repeat;
	background-size: cover;
	height: 500px !important;
	text-align: center;
	color: #fff;
}

#site-facts .circle{
  background: #27aae1;
  width: 660px;
  height: 660px;
  border-radius: 50%;
  margin: auto;
  position: relative;
  top: -180px;
  padding: 75px;
  overflow: hidden;
}

#site-facts .circle ul.facts-list{
  padding: 0;
  margin: 0;
}

#site-facts .circle ul.facts-list li{
  list-style: none;
}

#site-facts .circle ul.facts-list li:nth-child(1){
  margin: 5px auto 50px;
}

#site-facts .circle ul.facts-list li:nth-child(1)::before{
  content: "";
  border-left: 1px solid rgba(255,255,255, 0.2);
  height: 75px;
  position: absolute;
  top: 0;
}

#site-facts .circle ul.facts-list li:nth-child(1)::after{
  content: "";
  border-left: 1px solid rgba(255,255,255, 0.2);
  height: 34px;
  position: absolute;
}

#site-facts .circle ul.facts-list li:nth-child(2)::after{
  content: "";
  border-left: 1px solid rgba(255,255,255, 0.2);
  height: 34px;
  position: absolute;
}

#site-facts .circle ul.facts-list li:nth-child(3){
  margin: 50px auto 10px;
}

#site-facts .circle ul.facts-list li:nth-child(3)::after{
  content: "";
  border-left: 1px solid rgba(255,255,255, 0.2);
  height: 75px;
  position: absolute;
  bottom: 0;
}

#site-facts .circle .fact-icon{
  background: rgba(255,255,255, .1);
  height: 60px;
  width: 60px;
  border-radius: 50%;
  margin: auto;
}

#site-facts .circle .fact-icon{
  font-size: 50px;
}


/*==================================================
  Live Feed Section Homepage
  ==================================================*/

#live-feed{
  background: #fff;
	text-align: center;
}

#live-feed ul.online-users li {
  margin: 0;
}

/*feed-block and ul.online-users css available below*/


/*==================================================
  Newsfeed Left Sidebar
  ==================================================*/

/*Profile Card CSS*/

.profile-card{
  background: linear-gradient(to bottom, rgba(39,170,225,.8), rgba(28,117,188,.8)), url(./../images/covers/1.jpg) no-repeat;
  background-size: cover;
  width: 100%;
  min-height: 90px;
  border-radius: 4px;
  padding: 10px 20px;
  color: #fff;
  margin-bottom: 40px;
}

.profile-card a.profile-photo{
  border: 7px solid #fff;
  float: left;
  margin-right: 20px;
  position: relative;
  top: -30px;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  overflow: hidden;
  display: block;
}

.profile-card img.profile-photo {
  width: 100%;
  height: auto;
}
/*Newsfeed Links CSS*/

ul.nav-news-feed{
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 0 40px 0;
}

ul.nav-news-feed li{
  list-style: none;
  display: block;
  padding: 15px 0;
}

ul.nav-news-feed li div{
  position: relative;
  margin-left: 30px;
  }

ul.nav-news-feed li div::after{
  content: "";
  width: 100%;
  height: 1px;
  border-top: 1px solid #f1f2f2;
  position: absolute;
  bottom: -15px;
  left: 0;
}

ul.nav-news-feed li a{color: #6d6e71;}

ul.nav-news-feed li i{
  font-size: 18px;
  margin-right: 15px;
  float: left;
}

ul.nav-news-feed i.ion-ios-paper{color: #8dc63f;}

ul.nav-news-feed i.ion-ios-people{color: #662d91;}

ul.nav-news-feed i.ion-ios-people-outline{color: #ee2a7b;}

ul.nav-news-feed i.ion-chatboxes{color: #f7941e;}

ul.nav-news-feed i.ion-images{color: #1c75bc;}

ul.nav-news-feed i.ion-ios-videocam{color: #9e1f63;}

/*Chat Block CSS*/

#chat-block{
  margin: 0 0 40px 0;
  text-align: center;
}

#chat-block .title{
  background: #8dc63f;
  padding: 5px 20px;
  width: 70%;
  height: 30px;
  border-radius: 15px;
  position: relative;
  margin: 0 auto 20px;
  color: #fff;
  font-size: 14px;
  font-weight: 600;
}

ul.online-users{
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  margin: 0;
}

ul.online-users li{
  list-style: none;
  position: relative;
  margin: 3px auto !important;
  padding-left: 3px;
  padding-right: 3px;
}

ul.online-users li span.online-dot{
  background: linear-gradient(to bottom, rgba(141,198,63, 1), rgba(0,148,68, 1));
  border: none;
  height: 12px;
  width: 12px;
  border-radius: 50%;
  position: absolute;
  bottom: -6px;
  border: 2px solid #fff;
  left: 0;
  right: 0;
  margin: auto;
}


/*==================================================
  Newsfeed Right Sidebar
  ==================================================*/

.suggestions.affix{
  position:fixed !important;
}

.follow-user{
  margin-bottom: 20px;
}

.follow-user div{
  margin-left: 50px;
  position: relative;
}

.follow-user div::after{
  content: "";
  background: none;
  border-top: 1px solid #f1f2f2;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: -10px;
}


/*==================================================
  Create Post Box CSS
  ==================================================*/

.create-post{
  width: 100%;
  min-height: 90px;
  padding: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #f1f2f2;
}

.create-post .form-group{
  margin-bottom: 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.create-post .form-group .form-control {
  width: 100%;
}

.create-post .form-group img.profile-photo-md{
  margin-right: 10px;
}

.create-post .tools{
  padding: 8px 0 10px;
  width: 260px;
  margin-left: auto;
}

.create-post .tools ul.publishing-tools{
  display: inline-block;
  text-align:left;
  margin: 0;
  padding:5px 0;
}

.create-post .tools ul.publishing-tools li a{
  color: #6d6e71;
  font-size: 18px;
}

.create-post .tools ul.publishing-tools li a:hover{
  color: #27aae1;
}


/*==================================================
  Post Contents CSS
  ==================================================*/

.post-content{
  background: #f8f8f8;
  border-radius: 4px;
  width: 100%;
  border: 1px solid #f1f2f2;
  margin-bottom: 20px;
  overflow: hidden;
  position: relative;
}

.post-content img.post-image, video.post-video, .google-maps{
  width: 100%;
  height: auto;
}
.post-content a>img {
  width: 100%;
  height: auto;
}

.post-content .google-maps .map{
  height: 300px;
}

.post-content .post-container{
  padding: 20px;
}

.post-content .post-container .post-detail{
  margin-left: 65px;
  position: relative;
}

.post-content .post-container .post-detail .post-text{
  line-height: 24px;
  margin: 0;
}

.post-content .post-container .post-detail .reaction{
  position: absolute;
  right: 0;
  top: 0;
}

.post-content .post-container .post-detail .post-comment{
  display: inline-flex;
  margin: 10px auto;
  width: 100%;
}

.post-content .post-container .post-detail .post-comment img.profile-photo-sm{
  margin-right: 10px;
}

.post-content .post-container .post-detail .post-comment .form-control{
  height: 30px;
  box-shadow: none;
  margin: 7px 0;
  min-width: 0;
}


/*==================================================
  Nearby People CSS
  ==================================================*/

.people-nearby .google-maps{
  background: #f8f8f8;
  border-radius: 4px;
  border: 1px solid #f1f2f2;
  padding: 20px;
  margin-bottom: 20px;
}

.people-nearby .google-maps .map{
  height: 300px;
  width: 100%;
  border: none;
}

.people-nearby .nearby-user{
  padding: 20px 0;
  border-top: 1px solid #f1f2f2;
  border-bottom: 1px solid #f1f2f2;
  margin-bottom: 20px;
}
.nearby-user a>img{
  display: block;
  width: 100%;
  height: auto;
}

/*==================================================
  Friend List CSS = Newsfeed and Timeline
  ==================================================*/

.friend-list .friend-card{
  border-radius: 4px;
  border-bottom: 1px solid #f1f2f2;
  overflow: hidden;
  margin-bottom: 20px;
}

.friend-list .friend-card .card-info{
  padding: 0 20px 10px;
}

.friend-list .friend-card .card-info img.profile-photo-lg{
  margin-top: -60px;
  border: 7px solid #fff;
}


/*==================================================
  Chat Room CSS
  ==================================================*/

/*Chat Contact List*/

.chat-room ul.contact-list li{
  width: 100%;
  margin-bottom: 0;
}

.chat-room ul.contact-list li a{
  border: none;
  padding: 10px 0;
  border-bottom: 1px solid #f1f2f2 !important;
}

.chat-room ul.contact-list li a:hover, .chat-room ul.contact-list li a:active, .chat-room ul.contact-list li a:focus{
  border: none;
  border-bottom: 1px solid #8dc63f !important;
  border-radius: 0;
  cursor: pointer;
  background: none;
}

.chat-room ul.contact-list li a .contact{
  margin-left: 10px;
  margin-right: 10px;
}

.chat-room ul.contact-list li a .contact .msg-preview{
  margin-left: 50px;
}

.chat-room ul.contact-list li a .contact .msg-preview small{
  position: absolute;
  top: 20px;
